import React, { Component } from 'react'
import './Prevention.css'
var listOfImages =[];

export class Prevention extends Component {
  importAll(images) {
    return images.keys().map(images);
  }
  componentWillMount() {
      listOfImages = this.importAll(require.context('../img/prevention/', false, /\.(png|jpe?g|svg)$/));
  }

  render() {
    return (
      <div className="container py-4">
        <h1 className="mb-4 text-center">Prevention methods from Corona Virus</h1>
          {
            listOfImages.map((image, index) => 
              <div className="myth-img-container">
                {/* <h3 className="mb-3 pt-3">Prevention {index + 1}</h3> */}
                <img key={index} src={image} alt="Prevention from Corona Virus" className="mb-3 pt-3 w-100"></img>
              </div>
            )
          }
      </div>
    )
  }
}

export default Prevention
