import React, { Component } from 'react'
import news from './news.json'
import NewsBlock from './NewsBlock';
import './News.css'

export class News extends Component {

  renderNews = () => {
    let newsArray = [];
		news.newsList.map((newsItem, index) =>{
			newsArray.push(<NewsBlock newsItem={newsItem} key={index} />);
		});
		return newsArray;
	}

  render() {
    return (
      <div className="container" >
        <h1 className="text-center my-5">
          Ray of Hope
        </h1>
        <h5 className="mb-5">
          News from around the globe, which will make you believe that things will get better!
        </h5>
        <div className="row">
          <div className="col-12">
            {this.renderNews()}
          </div>
        </div>
      </div>
    )
  }
}

export default News
