import React, { Fragment } from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom'
import Covid from './Covid-19/Covid-19'
import Home from './Home/Home';
import Header from './Header/Header';
import Footer from './Footer/Footer';
import Myths from './Myths/Myths';
import Prevention from './Prevention/Prevention';
import Stress from './Stress/Stress';
import Stats from './Stats/Stats';
import './App.css'
import News from './RayOfHope/News';

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Fragment>
          <Header />
          <main>
            <Switch>
              <Route exact path="/corona-informatica/" component={Home} />
              <Route path="/corona-informatica/stats" component={Stats} />
              <Route path="/corona-informatica/covid-19" component={Covid} />
              <Route path="/corona-informatica/myths" component={Myths} />
              <Route path="/corona-informatica/prevention" component={Prevention} />
              <Route path="/corona-informatica/stress" component={Stress} />
              <Route path="/corona-informatica/news" component={News} />
            </Switch>
          </main>
          <Footer />
        </Fragment>
      </BrowserRouter>
    </div>
  );
}

export default App;
