import React, { Component } from 'react'
import './Myths.css'
var listOfImages =[];

export class Myths extends Component {
  state = {
    imagesLoaded: false
  }

  importAll(images) {
    return images.keys().map(images);
  }

  componentWillMount() {
    listOfImages = this.importAll(require.context('../img/myths/', false, /\.(png|jpe?g|svg)$/));
  }

  onLoad = () => {
    this.setState({
        imagesLoaded: true
    })
  }

  render() {
    const style = this.state.imagesLoaded ? {} : {visibility: 'hidden'}
    return (
      <div className="container py-4">
        <h1 className="mb-4 text-center">Common Myths about Corona Virus</h1>
          {
            listOfImages.map((image, index) => 
              <div className="myth-img-container">
                <h3 className="mb-3 pt-3">Myth {index + 1}</h3>
                <img key={index} style={style} onLoad={this.onLoad} src={image} alt="Myths about Corona Virus" className="mb-3 w-100"></img>
              </div>
            )
          }
      </div>
    )
  }
}

export default Myths
