import React, { Component } from 'react'
import covidImage from '../img/corona-image.png'
import coronaSpread from '../img/corona-spread.jpg'
import './Covid.css'

export class Covid extends Component {
  render() {
    return (
      <div className="container">
        <h1 className="text-center py-4">COVID-19</h1>
        <row className="mb-4">
          <p>COVID-19 is the infectious disease caused by the most recently discovered coronavirus. This new virus and disease were unknown before the outbreak began in Wuhan, China, in December 2019. Most estimates of the incubation period (the time between catching the virus and beginning to have symptoms of the disease) for COVID-19 range from 1-14 days, most commonly around five days. These estimates will be updated as more data become available.</p>
        </row>
        <div className="row mb-4">
          <div className="col-12 col-md-3">
            <img src={covidImage} alt="Covid-19" className="covid-img w-100 my-3" />
          </div>
          <div className="col-12 col-md-9">           
            <section className="my-3">
              <h4 className="mb-3">Should I worry about COVID-19?</h4>
              <p className="mb-2">Illness due to COVID-19 infection is generally mild, especially for children and young adults. However, it can cause serious illness: about 1 in every 5 people who catch it need hospital care. It is therefore quite normal for people to worry about how the COVID-19 outbreak will affect them and their loved ones.</p>
              <p>We can channel our concerns into actions to protect ourselves, our loved ones and our communities. First and foremost among these actions is regular and thorough hand-washing and good respiratory hygiene. Secondly, keep informed and follow the advice of the local health authorities including any restrictions put in place on travel, movement and gatherings.</p>
            </section>

          </div>
        </div>
        <div className="row mb-4">
            <div className="col-12 col-md-7">
              <section className="my-3">
                <h4 className="mb-3">What are the symptoms of COVID-19?</h4>
                <p className="mb-2">The most common symptoms of COVID-19 are fever, tiredness, and dry cough. Some patients may have aches and pains, nasal congestion, runny nose, sore throat or diarrhea. These symptoms are usually mild and begin gradually. </p>
                <p className="mb-2">
                  Some people become infected but donâ€™t develop any symptoms and don't feel unwell. Most people (about 80%) recover from the disease without needing special treatment. Around 1 out of every 6 people who gets COVID-19 becomes seriously ill and develops difficulty breathing. 
                </p>
                <p className="mb-2">
                  Older people, and those with underlying medical problems like high blood pressure, heart problems or diabetes, are more likely to develop serious illness. People with fever, cough and difficulty breathing should seek medical attention.
                </p>
              </section>

              <section className="my-3">
                <h4 className="mb-3">How does COVID-19 spread?</h4>
                <p className="mb-2">
                  People can catch COVID-19 from others who have the virus. The disease can spread from person to person through small droplets from the nose or mouth which are spread when a person with COVID-19 coughs or exhales. These droplets land on objects and surfaces around the person. 
                </p>
                <p className="mb-2">
                  Other people then catch COVID-19 by touching these objects or surfaces, then touching their eyes, nose or mouth. People can also catch COVID-19 if they breathe in droplets from a person with COVID-19 who coughs out or exhales droplets. This is why it is important to stay more than 1 meter (3 feet) away from a person who is sick. 
                </p>
                <p className="mb-2">
                  The main way the disease spreads is through respiratory droplets expelled by someone who is coughing. The risk of catching COVID-19 from someone with no symptoms at all is very low. However, many people with COVID-19 experience only mild symptoms. This is particularly true at the early stages of the disease. It is therefore possible to catch COVID-19 from someone who has, for example, just a mild cough and does not feel ill.
                </p>
              </section>
              
            </div>
            <div className="col-12 col-md-5">
              <img src={coronaSpread} className="w-100 my-3" alt="how corona spread"/>
            </div>
          </div>
        <div className="row mb-4">
          <div className="col-12">
            <a href="https://www.who.int/news-room/q-a-detail/q-a-coronaviruses" target="_blank" rel="noopener noreferrer" className="external-link-btn external-link-btn--light">More Questions</a>
          </div>
        </div>
        
      </div>
    )
  }
}

export default Covid
