import React, { Component } from 'react'

export class NewsBlock extends Component {
  render() {
    return (
      <a href={this.props.newsItem.newsLink} className="news-item d-flex p-3 mb-4" >
        <img src={this.props.newsItem.coverImage} alt={this.props.newsItem.title} className="news-image mr-4" />
        <div className="news-details">
          <h4 className="mb-2">{this.props.newsItem.title}</h4>
          <h5 className="news-subtitle">{this.props.newsItem.subtitle}</h5>
        </div>
      </a>
    )
  }
}

export default NewsBlock
