import React, { Component } from 'react'
var listOfImages =[];

export class Stress extends Component {

  state = {
    imagesLoaded: false
  }

  importAll(images) {
    return images.keys().map(images);
  }

  componentWillMount() {
    listOfImages = this.importAll(require.context('../img/stress/', false, /\.(png|jpe?g|svg)$/));
  }

  onLoad = () => {
    this.setState({
        imagesLoaded: true
    })
  }

  render() {
    const style = this.state.imagesLoaded ? {} : {visibility: 'hidden'}
    return (
      <div className="container py-4">
        <h1 className="mb-4 text-center">Coping with stress during COVID-19 Outbreak</h1>
          {
            listOfImages.map((image, index) => 
              <div className="myth-img-container">
                <img style={style} key={index} onLoad={this.onLoad} src={image} alt="coping with stress" className="mb-3 pt-3 w-100"></img>
              </div>
            )
          }
      </div>
    )
  }
}

export default Stress
