import React, { Component } from 'react'
import { NavLink } from "react-router-dom";
import Logo from '../img/logo.png'
import './Header.css'

export class Header extends Component {
  render() {
    return (
      <header>
        <nav className="container d-flex flex-column flex-lg-row align-items-center">
          <div className="logo-container">
            <NavLink exact to="/corona-informatica/" className="navbar-brand" href="#">
              <img src={Logo} alt="Corono Informatica" className="logo"/>
            </NavLink>
          </div>
          <div className="header-links d-flex">
            <NavLink to="/corona-informatica/stats" className="nav-link">Stats</NavLink>
            <NavLink to="/corona-informatica/covid-19" className="nav-link">COVID-19</NavLink>
            <NavLink to="/corona-informatica/myths" className="nav-link">Myths</NavLink>
            <NavLink to="/corona-informatica/prevention" className="nav-link">Prevention</NavLink>
            <NavLink to="/corona-informatica/stress" className="nav-link">Coping with Stress</NavLink>
            <NavLink to="/corona-informatica/news" className="nav-link">Ray of Hope</NavLink>
          </div>
        </nav>
      </header>
    )
  }
}

export default Header
